import React from 'react'
import ReactDOM from 'react-dom'
import $ from 'jquery'

class Button extends React.Component{
  localHandleClick(){
    this.props.localHandleClick(this.props.increment);
    //  class: forcus 
  }

  render(){
    return (
      //<button onClick={this.localHandleClick.bind(this)}>{this.props.increment}</button>
      <button onClick={this.localHandleClick.bind(this)}><div className={"forcus button_image0"+this.props.increment}></div></button>
    )
  }
}

class Result extends React.Component{
  render(){
    var skill_text = null,skill_title = null,skill_effect = null
    if (this.props.localCounter == 1) {
      skill_title = ""
      skill_text = "1.5"
      skill_effect = "1.5"
    }else if (this.props.localCounter == 2) {
      skill_title = ""
      skill_text = "10"
      skill_effect = " -10"
    }else if (this.props.localCounter == 3) {
      skill_title = ""
      skill_text = "1.5"
      skill_effect = "1.5"
    }else if (this.props.localCounter == 4) {
      skill_title = ""
      skill_text = ""
      skill_effect = ""
    }
    return(
<div className="char">
    <div className="left">
        <div className={'skill_image0'+this.props.localCounter}></div>
    </div>
    <div className="right">
        <div className="skill_title">
        <span className="item">
        
        </span>
        <span className="skill">
        {skill_title}
        </span>
        </div>
        <div className="skill_text">
        <span className="item">
        
        </span>
        <span className="skill">
        {skill_text}
        </span>
        </div>
        <div className="skill_effect">
        <span className="item">
        
        </span>
        <span className="skill">
        {skill_effect}
        </span>
        </div>
    </div>
</div>
    )
  }
}

class Main extends React.Component{
  constructor(props){
    super(props)
    this.state = {
      counter: 1,
    }
    this.handleClick = this.handleClick.bind(this)
  }
  handleClick(increment){
    this.setState({
      counter:increment
    });

  }
  render(){
    return(
      <div className="char_set">
        <div className="page_tag_area">
            <div className="tag_main"><h2></h2></div>
        </div>
        <div className="result_wrap">
          <Result localCounter={this.state.counter} />
        </div>
        <div className="btn_wrap">
            <div className={"btn_inline_wrap btn"+this.state.counter}>
                <Button localHandleClick={this.handleClick} increment={1} />
                <Button localHandleClick={this.handleClick} increment={2} />
                <Button localHandleClick={this.handleClick} increment={3} />
                <Button localHandleClick={this.handleClick} increment={4} />
            </div>
        </div>
        <form action="page3.html" name="char_post" className="form_area" id="my_form">
          <input id="counter_num" type="hidden" name="counter_num" value={this.state.counter} />
          <button className="connect page2" type="submit" id="btn_click" >
            START
          </button>
        </form>
      </div>
    )
  }
}

ReactDOM.render(
  <Main />,
  document.getElementById("page2")
)


$(function(){
  var param = urlParamGet();
  console.log(param.stage_id);
  $('#my_form').attr('action', "page3.html?id="+param.stage_id);
  $('#stage_id').val(param.stage_id);
});

/**---------- URL ----------**/
function urlParamGet(){
  var arg = new Object;
  var url = location.search.substring(1).split('&');

  for(var i=0; url[i]; i++){
    var k = url[i].split('=');
    arg[k[0]] = k[1];
  }

   return arg;
}



